.frame-vcard { // vcard popup - see Default.html for how this class is set
  position: relative;
}

.gris_staff {
  font-size: $gs_font_size_s1;
  a {
    text-decoration: none;
   }
  .tile {
    background: $gs_blue_15;
    &.person {
      h3 {
        font-size: 1.5rem;
        font-weight: 300;
        line-height: 1.8rem;
      }
    }
  }
  table {
    // gris_staff tables shall always have invisible/white borders,
    // no matter which .gesis-color-* theme is chosen for its content element
    --gesis-table-border-color: var(--gesis-color-white);
    
    border-collapse: separate;
    border-spacing: 0 0.4rem;
    tbody {
      tr {
        background: none;
      }
      td:not(.img) {
        padding: 0 1rem;
      }
      td {
        &.img {
          width: 0;
        }
      }
    }
  }
}

.gris-staff-view {
  margin-top: 5px;
  margin-bottom: 1rem;
  border: 1px solid $gs_blue_60;
  padding: $gs_pd_base;
  a {
    text-decoration: none;
  }
  img {
    margin-right: 0.5rem;
  }
}

.gs_vcard_toggle {
  position: absolute;
  top: 0;
  right: calc(-1 * var(--gesis-main-padding-x)); // let contact button occupy (main) container padding
  background: $gs_blue no-repeat url('img/icons/gs_contact_flyout.svg') .25rem .25rem;
  background-size: 1.5rem auto;
  background-position: top 3px right 4px;
  height: 2rem;
  width: 2rem;
  &:hover {
    cursor: pointer;
  }
  &[aria-expanded="false"] {
    .gs_vcard {
      display: none;
    }
  }
  &[aria-expanded="true"] {
    height: unset;
    width: unset;
    z-index: 6500;
    padding-bottom: 2rem; // add .gs_vcard's top 2rem to calculate correct height. Important for correct focus ring calculation
    outline: unset; // for some reason, when contact button is clicked, it always has pseudo state :focus-visible. Because this is annoying especially for the expanded="true" state, let's unset default dotted :focus-visible outline
    .gs_vcard {
      width: 30rem;
      background: $gs_blue_15;
      position: relative;
      right: 0;
      top: 2rem;
      cursor: default;
      /* z-index: 6000; */
     .gs_subline {
        padding: 0.5rem 1rem 0 1rem;
        margin: 0;
        font-size: 1.3rem;
        font-weight: normal;
      }
      ul {
        margin: 0;
        padding: 0;
        li {
          list-style: none;
          margin: 0;
          padding: 0;
          border-bottom: 0.2rem solid white;
          &:last-child {
            border: none;
          }
          .gris_staff_person_vcard {
            display: flex;
            justify-content: space-between;
            .gs_address {
              padding: $gs_pd_base;
              font-size: $gs_font_size_s1;
              a {
                text-decoration: none;
                padding-bottom: unset;
              }
              .gs_options {
                margin-top: 0.5rem;
              }
            }
            figure {
              margin: 0;
              img {
              width: 10rem;
              max-width: unset;
            }
            }
          }
        }
      }
    }
  }
}

@include media-breakpoint-down(md) {
  // extra rule because of main padding shrinking from 2rem to 1rem downwards from this breakpoint 
  .frame-vcard {
    /* examples:
    * https://www.gesis.org/cews/portfolio
    * https://www.gesis.org/forschung/angewandte-informatik/text-and-data-mining
    * https://www.gesis.org/allbus/allbus
    * FIXME: when first contact button, then banner image, then banner image overlays contact button
    */
    margin-bottom: 1rem;
    padding-bottom: 1rem;
  }
}
/* max-width: 575.98px equals @include media-breakpoint-down(sm) */
@media (max-width: 575.98px), (hover: none), (pointer: coarse) {
  .gs_vcard_toggle {
    &[aria-expanded="true"] {
      .gs_vcard {
        width: 100%;
      }
    }
  }
}

.gris_staff_detail {
  .gris_staff_detail_box {
    padding: $gs_pd_base;
    background: $gs_blue_15;
    margin-bottom: 1rem;
    a {
      text-decoration: none;
    }
  }
  h1 {
    font-size: 1.5rem;
  }
  h3 {
    font-size: 1.3rem;
    border-bottom: 0.1rem solid $gs_blue;
    margin-bottom: 1.0rem;
    padding-top: 1.5rem;
  }
}
