.eventnews-month {
  background-color: var(--gesis-color-grey-10);
  padding: 0.8rem;

  .pager {
    .bi {
      vertical-align: middle;
      font-size: x-large;
    }
  }

  table {
    border-collapse: separate;
    border-spacing: 0.3rem;
    table-layout: fixed;
    margin-bottom: $gs_pd_base;
    
    a {
      text-decoration: none;
    }

    tr {
      &.days {
        th {
          color: white;
          background-color: var(--gesis-normal-color);
        }
      }
      
      th {
        border: none;
        padding: 0;
        text-align: center;
        height: 2rem;
        vertical-align: middle;
      }
      
      td {
        border: none;
        border-top: 0.45rem solid;
        background-color: var(--gesis-color-blue-30);
        color: var(--gesis-color-blue);
        padding: 0;
        text-align: center;
        height: 2rem;
        line-height: 2rem; /* make nested a's clickable area full width */
        vertical-align: middle;
        
        &.hasevents {
          border-top: 0.45rem solid var(--gesis-attention-color);
          background-color: white;
        }
        &.selected {
          background-color: var(--gesis-attention-20-color);
        }
        &.today {
          * {
            color: var(--gesis-attention-color);
          }
        }
        
        a {
         font-weight: bolder;
         /* make full cell clickable, not only link text */
         display: block;
         width: 100%;
        }
      }
    }
  }
}

.eventnews-list {
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    li {
      background-color: var(--gesis-color-grey-10);
      div {
        padding: 0.5rem 0.8rem;
        // margin-bottom: 0;
        &:first-child { // first div always is date-time range information for particular event
          color: $gs_blue;
          background-color: var(--gesis-color-blue-30);
        }
      }
    }
  }
}
