.slider-testimonials {
  .tns-outer .tns-controller {
    justify-content: center;
    
    button.tns-autoplay {
      margin-right: unset;
    }
    
    .tns-controls {
      display: none;
    }
  }
  
  .tns-nav  {
    order: 1;
    margin-top: unset;
    margin-bottom: 1rem;
  }
}

.gs_testimonial {
  max-width: 40rem;
  
  .quote-container {
    background-color: #adbdcc;
    font-family: $gs_font_serif;
    text-align: center;
    /* hyphens: auto; */
    
    > * {
      padding: 1rem;
    }
    
    blockquote {
      font-size: 1.4rem;
      font-style: italic;
      color: white;
      
      &::before {
        font-family: $gs_font_sans;
        display: block;
        content: '';
        font-size: 13rem;
        position: relative;
        top: 3.5rem;
        left: -1.5rem;
        color: $gs_blue;
        line-height: 7rem;
      }
    }
    
    figcaption {
      &::before {
        content: '';
        margin-right: 0.5rem;
      }
      &::after {
        content: '';
        margin-left: 0.5rem;
      }
    }
  }
  
  .image-container {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
