.tabs, .tabs-style { // .tabs-style to differentiate non-interactive (e.g. Staff Directory) vs. interactive (.tabs) elements
  > [role="tablist"] {
    display: flex;
    flex-wrap: wrap;
    // background: $gs_blue_60;
    [role="tab"] {
      flex: 1 1 auto;
      // BEGIN magic for button separators that work for buttons in row and stacked
      margin-left: -1px;
      border-left: 1px solid white;
      border-top: 1px solid white;
      // border-top-right-radius: 10px; // rounded corner for affordance highlighting tab character
      // END magic
      &[aria-selected="true"] {
        background-color: var(--gesis-attention-color);
        cursor: default;
      }
    }
  }
  
  > [role="tabpanel"] {
    border: 1px solid $gs_blue_60;
    border-top: none;
    padding: 1rem;
    &:focus-visible {
      outline: var(--gesis-attention-color) 2px dotted;
      outline-offset: -2px;
    }
    > *:last-child {
      margin-bottom: 0;
      padding-bottom: 0;
    }
  }
}
