// $primary: #ff00ff;
$primary: $gs_blue;
$body-color: $gs_black;

$enable-rounded: false;
$enable-negative-margins: true;

$font-family-sans-serif: $gs_font_sans;
$font-family-monospace: $gs_font_code;
$font-size-base: $gs_font_size_base;

$line-height-base: $gs_line_height_base;

$h1-font-size: $gs_font_size_b6;
$h2-font-size: $gs_font_size_b3;
$h3-font-size: $gs_font_size_base;
// $h3-font-size: $gs_font_size_b2;
$h4-font-size: $gs_font_size_base;
$h5-font-size: $gs_font_size_base;

// $headings-font-weight: 300;

// $link-decoration: underline solid from-font; // from-font to have light fonts have light underline

// see https://stackoverflow.com/a/68303393/923560
// see https://stackoverflow.com/a/67958494/923560
$min-contrast-ratio: 0;

$enable-cssgrid: true;

$spacer: 1rem;
$spacers: (
  0: 0,
  1: $spacer * .25,
  2: $spacer * .5,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 3,
  gesis-1: $spacer,
  gesis-2: $spacer * 2,
);

@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";

$gesis-colors: (
  "blue": $gs_blue,
  "orange": $gs_orange,
  "gesis": $gs_blue,
  "cews": $gs_cews,
  "facebook": #3b5998,
  "twitter": #55acee,
  "linkedin": #0077b5,
);

$theme-colors: map-merge($theme-colors, $gesis-colors);

// removing xxl size
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
);

// removing xxl size
$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px
);

