.dropdown-toggle {
  white-space: normal; // Break long dropdowns, e.g. in staff directory when department is "Knowledge Technologies for the Social Sciences"
}

// reset to Bootstrap style for interfering GESIS styles
input[type=radio].form-check-input, input[type=checkbox].form-check-input {
  width: 1em;
  height: 1em;
  margin-top: 0.25em;
  transform: unset;
  padding: unset;
}

// reset to Bootstrap style for interfering GESIS styles
// see http://localhost/development/gris-service Frderer drop down versus debug copy-page-id-to-clipboard form button
button.btn {
  // margin: unset;
  margin: auto;
}

.table-responsive {
  /*
   augment .table-responsive-wrapped table elements with overflow scroll shades
   on the left and right side.
   These are handy to make users on small screens aware that the table is
   horizontally scrollable.
   
   Has tolerable bug in Firefox in small-screen-wide-table, see https://bugzilla.mozilla.org/show_bug.cgi?id=1488080
   Example: http://localhost/institut/mitarbeiterverzeichnis/orga/table/63/all
  */
  margin-left: -1rem;
  margin-right: -1rem;
  display: flex;
  &::before {
    content: "";
    position: sticky;
    left: 0;
    top: 0; // in case overscroll-y is used with a fixed height, stretch along the whole box
    bottom: 0; // in case overscroll-y is used with a fixed height, stretch along the whole box
    z-index: 2; // in case a sticky table header is used, position overflow marker above sticky table header
    min-width: 1rem;
    /* or padding-right: 1rem */;
    /* background-color: #0000ff88; */
    // background-image: linear-gradient(to right, white, transparent);
    // -webkit-linear-gradient and -moz-linear-gradient because of iOS problems with "to" and "transparent", see https://caniuse.com/?search=linear-gradient
    // https://stackoverflow.com/a/34760072/923560 , https://stackoverflow.com/a/59132551/923560
    background-image: -webkit-linear-gradient(left, var(--gesis-background-color) 0%, rgba(255, 255, 255, 0) 100%);
    // background-image: -moz-linear-gradient(to right, var(--gesis-background-color), rgba(255, 255, 255, 0));
    // background-image: linear-gradient(to right, var(--gesis-background-color), transparent); // this is the most modern approach
    background-image: linear-gradient(to right, var(--gesis-background-color), rgba(255, 255, 255, 0));
  }
  &::after {
    content: "";
    position: sticky;
    right: 0;
    top: 0; // in case overscroll-y is used with a fixed height, stretch along the whole box
    bottom: 0; // in case overscroll-y is used with a fixed height, stretch along the whole box
    z-index: 2; // in case a sticky table header is used, position overflow marker above sticky table header
    min-width: 1rem;
    /* or padding-right: 1rem */;
    /* background-color: #ff000088; */
    // -webkit-linear-gradient and -moz-linear-gradient because of iOS problems with "to" and "transparent", see https://caniuse.com/?search=linear-gradient
    // https://stackoverflow.com/a/34760072/923560 , https://stackoverflow.com/a/59132551/923560
    background-image: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, var(--gesis-background-color) 100%);
    // background-image: -moz-linear-gradient(to right, rgba(255, 255, 255, 0), var(--gesis-background-color));
    // background-image: linear-gradient(to right, transparent, var(--gesis-background-color)); // this is the most modern approach
    background-image: linear-gradient(to right, rgba(255, 255, 255, 0), var(--gesis-background-color));
  }
  
  margin-bottom: 1rem;
  &:last-child {
    margin-bottom: 0rem;
  }
}

.clear-none {
  clear: none !important;
}

.clear-left {
  clear: left !important;
}

.clear-right {
  clear: right !important;
}

.clear-both {
  clear: both !important;
}

@media (hover: none) {
  .d-hover-none-block {
    display: block !important;
  }
}

@media (pointer: coarse) {
  .d-pointer-coarse-block {
    display: block !important;
  }
}

@media (pointer: none) {
  .d-pointer-none-block {
    display: block !important;
  }
}

 @include media-breakpoint-down(md) {
  .btn-group-vertical-md-down {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    
    > .btn,
    > .btn-group {
      width: 100%;
    }
    
    > .btn:not(:first-child),
    > .btn-group:not(:first-child) {
      margin-top: -$btn-border-width;
    }
    
    // Reset rounded corners
    > .btn:not(:last-child):not(.dropdown-toggle),
    > .btn-group:not(:last-child) > .btn {
      @include border-bottom-radius(0);
    }
    
    > .btn ~ .btn,
    > .btn-group:not(:first-child) > .btn {
      @include border-top-radius(0);
    }
  
  }
}
