main {
  > .frame {
    &.frame-accordion, &.frame-type-gesis-web-accordion {
      /*
      accordions are special in that as direct children of the
      main container, they shall have a vertical margin of only 1rem instead
      of the default 2rem.
      */
      margin-top: 1rem;
      margin-bottom: 1rem;
      
      &:first-child {
        margin-top: 0rem;
      }
      &:last-child {
        margin-bottom: 0rem;
      }
    }
  }
}

.accordion {
  > button {
    width: 100%;
    display: flex;
    text-align: left;
    &[aria-expanded="false"] {
      &::before {
        // content: "";
        font-family: bootstrap-icons;
        content: '\f229';
        padding-right: 0.8rem;
      }
      + div {
        display: none;
      }
    }
    &[aria-expanded="true"] {
      &::before {
        // content: "";
        font-family: bootstrap-icons;
        content: '\f235';
        padding-right: 0.8rem;
      }
      + div {
        border: 1px;
        border-top: 0px;
        border-color: $gs_blue_60;
        border-style: solid;
        padding: $gs_pd_base;
      }
    }
    > img {
      // optional icons inside toggle button
      width: 20px;
      height: 20px;
      margin-right: 0.8rem;
    }
  }
}
