.tx-powermail {
  form {
    label {
      cursor: pointer;
    }
    background-color: $gs_grey_10;
    padding: $gs_pd_base;
    h3 {
      font-size: $gs_font_size_b6;
      line-height: $gs_line_height_b3;
      font-weight: normal;
    }
    > fieldset {
      /* a powermail "page" */
      > legend {
        /* powermail "page" title */
        font-size: $gs_font_size_b1;
        line-height: $gs_line_height_base;
      }
      > div {
        /* this div ".powermail_fieldwrap" wraps an input component (or component group (checkbox group, radiogroup)) and its label to the left */
        display: flex;
        padding: $gs_pd_s2 $gs_pd_base; // give some padding so that content has some air when focus-within highlighting is active.
        margin-left: -#{$gs_pd_base}; // in focus-within highlighting, snuggle highlighting directly on container border
        margin-right: -#{$gs_pd_base}; // in focus-within highlighting, snuggle highlighting directly on container border
        &.powermail_fieldwrap_type_reset, input[type="reset"] { // rules like this required for submit button layout being correct with or without leading reset button
          float: left; // if a reset button is given at all, at least move it out of sight by floating left
          width: unset;
        }
        &.powermail_fieldwrap_type_submit, input[type="submit"] { // rules like this required for submit button layout being correct with or without leading reset button
          float: right;
          width: unset;
        }
        &.powermail_fieldwrap_type_reset, &.powermail_fieldwrap_type_submit {
          background-color: unset !important; // for reset and submit buttons, overwrite default powermail form element focus-within background-color highlighting
          padding-bottom: 0;
          input[type="submit"], input[type="reset"] {
            overflow-wrap: anywhere; // for pathological cases, e.g. http://localhost/development/powermail, mobile view, reset and submit button
            white-space: break-spaces; // for pathological cases, e.g. http://localhost/development/powermail, mobile view, reset and submit button
          }
        }
        > label {
          /* left-side label for input component set. Shall not match an individual checkbox's and radiobutton's label */
          flex: 1; // label gets 1 of 4 horizontal space
          text-align: right;
          // font-weight: bold; // make field name and explanation visually distinct from field values and field value remarks
          margin-right: $gs_pd_base;
          .mandatory {
            font-weight: normal;
          }
        }
        > div, > fieldset { // this div is class "powermail_field" .. TODO: is this < fieldset actually used -> necessary at all?
          /* these wrap the actual input(s) */
          flex: 3; // div/fieldset get 3 of 4 horizontal space
          
          // make div/fieldset nested flex containers. This is required for changing the CSS "order" attribute of the optional validation error message
          display: flex;
          flex-direction: column;
          > * {
            width: 100%;
          }
          &[role="group"], &[role="radiogroup"] { /* matches checkboxes and radios */
            padding-left: 0;
            padding-top: 0.2rem;
            label {
              /* the following magic is for having radio and checkbox text not float under their respective input element, see https://stackoverflow.com/a/46203459/923560 */
              display: inline-flex;
              input {
                margin-right: 1.0rem;
                margin-top: 0.1rem;
                flex-shrink: 0;
                position: relative;
                left: 8px;
              }
            }
            .powermail_field_error_container {
              padding-left: 4px; // align validation error messages with their checkboxes and radios
            }
          }
          > :only-child {
            // input element without placeholder which does not (yet) have validation error message container, therefore reserve space
            margin-bottom: 1.4rem;
          }
          > .powermail-placeholder {
            &:last-child {
              // there is a placeholder text element and its input field does not have validation errors (yet),
              // therefore reserve space for validation error messages so that the browser viewport doesn't
              // jump when the error messages appear
              margin-bottom: 1.4rem;
            }
            &:not(:last-child) {
              // there is a placeholder text element and its input field has validation errors
              margin-bottom: 0rem;
            }
            
          }
          > .powermail_field_error_container {
            &:empty {
              // reserve space for potential error messages
              height: 1.4rem;
            }
          }
          
        }
        .powermail_field_error {
          background-color: $gs_orange_20;
        }
        .powermail-errors-list {
          margin: 0;
          padding: 0;
          
          &:empty {
            // reserve vertical space for potential upcoming validation errors
            // otherwise the viewport will jump around when showing and removing validation error messages
            height: 1.4rem;
          }
          li {
            margin: 0;
            padding: 0;
            list-style-type: none;
            color: $gs_orange;
          }
        }
        &:focus-within {
          background-color: $gs_grey_20;
        }
      }
      &:last-of-type {
        // target the last fieldset of inputs. using last-of-type
        // instead of last-child, as powermail adds a hidden input field after the last fieldset
        div:last-child {
          // last input element of form, usually submit button, shall not provide bottom padding. For SuiteCRM HTML forms
          padding-bottom: 0;
        }
      }
    }
  }
  
  ::placeholder {
    /* input placehoder texts are bad for user experience and accessibilility. Instead, use JavaScript to always display them below input field */
    /* for Chrome and Firefox */
    visibility: hidden;
    opacity: 0;
    color: transparent;
  }
  .powermail-serverside-validation-errors {
    background-color: $gs_orange_20;
    padding: $gs_pd_s2;
    ul {
      margin: 0;
      li {
        margin: 0;
        padding: 0;
        list-style-type: none;
      }
    }
  }
  .powermail_confirmation {
    /* confirmation-before-conclusion page */
    .btn-group {
      /* back and continue buttons are wrapped in this div */
      display: flex;
      justify-content: space-between;
      form {
        padding: 0;
        margin: 0;
        > input[type="submit"], > input[type="reset"] {
          margin: 0;
        }
      }
    }
  }
  /* reset bootstrap class collision */
  .container-fluid {
    all: revert;
  }
}

input {
  border-style: solid;
  border-color: $gs_blue;
  border-width: 0.1rem;
  padding: $gs_pd_s4 $gs_pd_s2;
  // margin: $gs_pd_s2 0;
  &[type="radio"], &[type="checkbox"] {
    margin: 0;
    /* width: 20px */;
    height: 20px /* setting height only, not additionally width, magically and correctly aligns radio and checkbox properly vertically on top position. Important in scnearios where there is multi-line text floating next to it */;
    transform: scale(1.5);
    background-color: $gs_white;
    &:focus-visible {
      /* checkboxes and radios are surprisingly Browser-proprietary to style, therefore stick to their default styling */
      outline: auto;
    }
  }
  &[type="submit"], &[type="reset"], &[type="button"] {
    // also see gesis-web.scss rule "button, .gs_button"
    background-color: var(--gesis-normal-color);
    color: $gs_white;
    width: auto;
    cursor: pointer;
    &:hover {
      background-color: var(--gesis-attention-color);
      border-color: var(--gesis-attention-color);
    }
    &:focus-visible {
      outline: white 2px dotted;
      outline-offset: -2px;
    }
  }
  &:focus-visible {
    outline: var(--gesis-attention-color) 2px solid;
    outline-offset: -2px;
    /* next properties are for having focus ring in front of adjacent elements, see https://stackoverflow.com/a/11802502/923560 */;
    position: relative;
    z-index: 1;
  }
}

textarea {
  border-color: $gs_blue;
  border-width: 0.1rem;
  padding: $gs_pd_s4 $gs_pd_s2;
  &:focus-visible {
    outline: var(--gesis-attention-color) 2px solid;
    outline-offset: -2px;
  }
}

select {
  border-color: $gs_blue;
  border-width: 0.1rem;
  padding: $gs_pd_s4 $gs_pd_s2;
  &:focus {
    outline: var(--gesis-attention-color) 2px solid;
    outline-offset: -2px;
  }
  &:hover:not(:disabled) {
    cursor: pointer;
  }
}

/* reset iOS Safari's and iOS Chrome's default form elements rounded corners user agent style, see https://stackoverflow.com/a/2918716/923560 , https://www.daretothink.co.uk/stop-ios-styling-your-input-fields-and-buttons/ . iOS select element is special: https://stackoverflow.com/questions/7638677/how-can-i-remove-the-gloss-on-a-select-element-in-safari-on-mac */;
textarea, select, input[type="button"], input[type="submit"], input[type="reset"], input[type="text"], input[type="search"], input[type="password"], input[type="tel"], input[type="url"], input[type="email"] {
  border-radius: 0;
}

textarea, input[type="button"], input[type="submit"], input[type="reset"], input[type="text"], input[type="search"], input[type="password"], input[type="tel"], input[type="url"], input[type="email"] {
  -webkit-appearance: none;
}

@include media-breakpoint-down(lg) {
  .tx-powermail {
    form {
      > fieldset {
        > div {
          /* this div ".powermail_fieldwrap" wraps an input component and its label */
          flex-direction: column;
          > label {
            text-align: left;
          }
        }
        
        [role="group"] {
          margin: 0 /* checkboxes come with their own margin */;
        }
        
        [role="radiogroup"] {
          margin: 0 /* radios come with their own margin */;
        }
      }
    }
  }
}
