@import 'gesis-constants.scss';
@import 'gesis-color.scss';
@import '../font/source/source.scss';
@import '../font/gesis-web-icons/style.css';
@import '@fancyapps/ui';
@import 'gesis-bootstrap.scss';
// @import 'tablesorter/dist/css/scss/theme.scss';
// @import 'tablesorter/dist/css/theme.default.min.css';
@import 'node_modules/highlight.js/scss/base16/default-dark.scss';
@import '../../node_modules/datatables.net-bs5/css/dataTables.bootstrap5.min.css';

:root {
  --gesis-padding-s5: #{$gs_pd_s5};
  --gesis-padding-s4: #{$gs_pd_s4};
  --gesis-padding-s3: #{$gs_pd_s3};
  --gesis-padding-s2: #{$gs_pd_s2};
  --gesis-padding-s1: #{$gs_pd_s1};
  --gesis-padding-base: #{$gs_pd_base};
  --gesis-padding-b1: #{$gs_pd_b1};
  --gesis-padding-b2: #{$gs_pd_b2};
  
  --gesis-color-blue: #{$gs_blue};
  --gesis-color-blue-60: #{$gs_blue_60};
  --gesis-color-blue-30: #{$gs_blue_30};
  --gesis-color-blue-15: #{$gs_blue_15};
  --gesis-color-orange: #{$gs_orange};
  --gesis-color-orange-80: #{$gs_orange_80};
  --gesis-color-orange-60: #{$gs_orange_60};
  --gesis-color-orange-40: #{$gs_orange_40};
  --gesis-color-orange-20: #{$gs_orange_20};
  --gesis-color-white: #{$gs_white};
  --gesis-color-grey: #{$gs_grey};
  --gesis-color-grey-80: #{$gs_grey_80};
  --gesis-color-grey-60: #{$gs_grey_60};
  --gesis-color-grey-40: #{$gs_grey_40};
  --gesis-color-grey-20: #{$gs_grey_20};
  --gesis-color-grey-10: #{$gs_grey_10};
  --gesis-color-grey-5: #{$gs_grey_5};
  --gesis-color-black: #{$gs_black};
  --gesis-color-cews: #{$gs_cews};
  --gesis-color-cews-20: #{$gs_cews_20};
  
  --gesis-background-color: #{$gs_grey_80};
  --gesis-accent-background-color: #{$gs_white};
  --gesis-normal-color: #{$gs_blue};
  --gesis-attention-color: #{$gs_orange};
  --gesis-attention-20-color: #{$gs_orange_20};
  
  --gesis-font-sans: #{$gs_font_sans};
  --gesis-font-serif: #{$gs_font_serif};
  --gesis-font-code: #{$gs_font_code};
  
  --gesis-standard-opacity: #{$gs_standard_opacity};
  --gesis-backdrop-opacity: #{$gs_backdrop_opacity};
  
  accent-color: var(--gesis-normal-color); // color checkboxes and radios in GESIS blue
  scroll-behavior: smooth;
}

// required for gesis-dialog.scss because of
// https://stackoverflow.com/a/63322762
// https://developer.mozilla.org/en-US/docs/Web/CSS/::backdrop
// "::backdrop neither inherits from nor is inherited by any other elements"
::backdrop {
  --gesis-backdrop-opacity: #{$gs_backdrop_opacity};
}

* {
  &:focus-visible {
    outline: white 2px dotted;
    outline-offset: -2px;
  }
}

body {
  padding-left: 0 !important;
  padding-right: 0 !important;
  background-color: var(--gesis-background-color);
  text-rendering: optimizeLegibility;
  // line-height: $gs_line_height_base;
  word-wrap: break-word; /* so that e.g. long link texts don't overflow */
  footer {
    padding: $gs_pd_base 0;
    color: $gs_white;
    a {
      color: $gs_white;
    }
    .gs_footerlinks {
      a {
        border-right-style: solid;
        border-width: thin;
        padding-left: 0.1rem;
        padding-right: 0.3rem;
        &:hover {
          border-color: white;
        }
      }
    }
  }
}

pre, code, tt {
  font-family: $gs_font_code;
}

pre.sample-code code {
  max-height: 50vh;
}

p {
  &:last-child {
    margin-bottom: 0;
  }
}

blockquote {
  font-style: italic;
  font-family: $gs_font_serif;
}

figure {
  margin: 0;
}

h3, h4, h5, h6 {
  // for h4 and h5 examples: http://localhost/en/issp/home/contacts
  font-weight: bold;
}

a {
  color: var(--gesis-normal-color);
  text-underline-offset: 0.1rem;
  text-decoration-thickness: from-font; // required for thin font links to have thin underline ... http://localhost/angebot ... http://localhost/en/research/applied-computer-science
  &:hover {
    color: var(--gesis-attention-color);
  }
  &:focus-visible {
    outline-offset: 0px;
    outline-color: var(--gesis-attention-color);
    img {
      /*
        if an img is descendant of an a element, and that a element has focus,
        then also give an explicit focus outline to that img.
        Important for example for home landing page teaser images
        and CKEditor linked gallery images such as
        home landing page social media links in the bottom:
        http://localhost/home
      */
      outline-offset: -2px;
      outline-color: var(--gesis-attention-color);
      outline-width: 2px;
      outline-style: dotted;
    }
  }
  &.btn:hover {
    color: white; // Bootstrap link button text shall stay white on hovering
  }
  &[data-fancybox] {
    // Fancybox gallery preview images shall not have underlines (or have them leak between preview images)
    text-decoration: none;
  }
}

button, .gs_button {
  // also see gesis-form.scss rule "&[type="submit"], &[type="reset"], &[type="button"]"
  background-color: var(--gesis-normal-color);
  color: var(--gesis-color-white);
  border: none;
  word-break: break-word;
  // margin: $gs_pd_s2 $gs_pd_s4;
  padding: $gs_pd_s3 $gs_pd_s1;
  // font-family: inherit;
  // font-size: $gs_font_size_base;
  
  // experiment in the hopes of deactivating unintended zoom-on-double-tap-on-buttons
  // see https://stackoverflow.com/a/53236027/923560
  touch-action: manipulation;
  
  &:hover {
    background-color: var(--gesis-attention-color);
    color: white; // for .gs_button
  }
  &:focus-visible {
    outline: white 2px dotted;
    outline-offset: -2px;
  }
  &+ button {
    margin-left: $gs_pd_s2; // give margin between consecutive buttons
  }

}
.gs_button {
  text-decoration: none;
  display: inline-flex;
  text-align: center; // important for both container flex-wrap on or off
  align-items: center; // important for both container flex-wrap on or off
  justify-content: center; // important for both container flex-wrap on or off
}

// examples:
// https://www.gesis.org/institut/presse-und-medien/social-media (social media wall)
// https://www.gesis.org/cews/cews-home/20-jahre-cews (embedded Youtube video)
// https://www.gesis.org/gesis-training/home
// only give iframe 100% iff iframe does not specify its own width attribute
// (found to be important for https://www.gesis.org/cews/cews-home/20-jahre-cews CKEditor scenario
iframe {
  max-width: 100%; // needed for e.g. several YouTube iframes in CKEditor gallery not to overflow their boundaries
  &:not([width]) {
    width: 100%;
  }
}

img, video {
  vertical-align: middle; // Bootstrap 5 puts images on vertical middle, therefore videos shall be aligned on middle as well
  // handle odd corner case for even alignment with imagees-in-gridelements-boxes-in-grid-elements-box
  // example: http://localhost/datenservices/home
  max-width: 100%;
  object-fit: contain;
}

nav {
  img {
    vertical-align: middle;
  }
}

main {
  --gesis-background-color: #{$gs_white};
  --gesis-accent-background-color: #{$gs_blue_15};
  --gesis-main-padding-x: #{$gs_pd_b2};
  
  position: relative; /* for vcard contact box to have a relative frame and not flow out of main area */
  background-color: var(--gesis-background-color);
  
  padding-top: $gs_pd_b2;
  padding-bottom: $gs_pd_b2;
  
  padding-left: var(--gesis-main-padding-x);
  padding-right: var(--gesis-main-padding-x);
}

.faded {
  position: relative;
  &::before {
    content: '';
    background-color: $gs_black;
    z-index: $gs_z_fader;
    width: 100%;
    height: 100%;
    opacity: var(--gesis-backdrop-opacity);
    position: absolute;
    top: 0;
    left: 0;
  }
}

.unfaded {
  position: relative;
  z-index: $gs_z_fader + 1;
}

.headline {
  // text-align: center;
  background-color: $gs_grey_60;
  color: $gs_white;
  padding: $gs_pd_s2 $gs_pd_base;
  h1 {
    font-size: $gs_font_size_base;
    line-height: $gs_line_height_base;
    margin: 0;
  }
  a {
    color: $gs_white;
    &:hover {
      color: var(--gesis-attention-color);
    }
  }
}

.gs_header_main {
  background-color: $gs_white;
  border-top: $gs_pd_base solid $gs_blue; /* Dachbalken */
}

.gs_topnav {
  ul {
    li {
      padding-left: 1rem;
      list-style-type: none;
      font-weight: bold;
    }
  }
}

.gs_rootline {
  line-height: $gs_line_height_b2;
  font-size: $gs_font_size_s1;
  font-style: italic;
  color: $gs_grey_40;
  padding-left: 1.25rem; // + 0.75rem parent Bootstrap container gutter = 2rem
  > ol, > ul {
    li {
      // display: inline;
      list-style-type: none;
      padding: 0 $gs_pd_s2;
    }
    > li::after {
      // content: '';
      font-style: normal;
      font-family: bootstrap-icons;
      content: '\f231';
      position: relative;
      top: 0.1rem;
      padding-left: 1rem;
      font-size: 0.7rem;
    }
    > li:last-of-type::after {
      content: '';
    }
    > li.gs_dropdown_toggle:last-of-type::after {
      // content: '';
      font-style: normal;
      font-family: bootstrap-icons;
      content: '\f229';
      padding-left: 0.25rem;
    }
    .gs_dropdown_toggle {
      position: relative;
      &:focus-visible {
        outline: var(--gesis-attention-color) 2px dotted;
        outline-offset: -2px;
      }
      .gs_dropdown_content {
        display: none;
      }
    }
    .gs_dropdown_toggle[aria-expanded="true"] {
      background-color: $gs_blue_15;
      &:focus-visible {
        outline: unset;
      }
      &:hover {
        cursor: pointer;
      }
      .gs_dropdown_content {
        display: block;
        position: absolute;
        z-index: $gs_z_fader_highlight;
        width: max-content; // so that pop-up can grow beyond toggle if content is bigger
        min-width: 100%; // so that pop-up can grow if its smaller than parent li container
        background-color: $gs_blue_15;
        left: 0;
        padding: 0;
        // margin: 0;
        li {
          // list-style-type: none;
          padding: 0;
          // margin: 0;
          a {
            padding: $gs_pd_s2 $gs_pd_s1;
            display: block;
            // width: auto;
            &:hover {
              background-color: var(--gesis-attention-color);
              color: $gs_white;
            }
          }
          a[aria-selected="true"] {
            outline: var(--gesis-attention-color) 2px dotted;
            outline-offset: -2px;
          }
        }
      }
    }
  }
}

.gs_box {
  --gesis-background-color: #{$gs_grey_10};
  background-color: $gs_grey_10;
  padding: $gs_pd_base;
  overflow: hidden; // when box content is too wide, e.g. text-without-spaces, don't change box's width
  .frame {
    padding: 0;
  }
  p {
    hyphens: auto;
    /*
     hyphenate-limit-chars not supported in Chrome und Firefox yet
     as of 2021-11-25
     Should be useful to remedy awkward hyphenation e.g. in Firefox
     https://test.gesis.org/allbus/allbus/veranstaltungen/40-jahre-allbus-ringvorlesung
     mouseover box gets hyphenated as "Hier get es weiter zur An-meldung"
     https://www.w3.org/TR/css-text-4/#hyphenate-char-limits
     https://bugzilla.mozilla.org/show_bug.cgi?id=1521723
     https://bugs.chromium.org/p/chromium/issues/detail?id=924069
    */
    hyphenate-limit-chars: auto 5;
    word-break: normal;
  }
}

.gs_box_content {
  padding: $gs_pd_base;
  .frame { //.ce-default
    margin: 0;
    padding: 0;
  }
}

.text-left {
  // at minimum, provided for CKEditor
  // Bootstrap 5 provides the semantically superior (RTL vs. LTR) ".text-start": https://getbootstrap.com/docs/5.1/utilities/text/#text-alignment
  text-align: left;
}

.text-justify {
  // at minimum, provided for CKEditor
  // Bootstrap 5 does not provide ".text-justify" for their own opinioned reasons: https://getbootstrap.com/docs/5.1/utilities/text/#text-alignment
  text-align: justify;
}

.text-right { // CKEditor
  // at minimum, provided for CKEditor
  // Bootstrap 5 provides the semantically superior (RTL vs. LTR) ".text-end": https://getbootstrap.com/docs/5.1/utilities/text/#text-alignment
  text-align: end;
}

// example: https://www.gesis.org/wahlen/wahlen-home
// example: https://www.gesis.org/cews/cews-home/20-jahre-cews
.gs_image_box {
  position: relative; // relevant for optional gs_shade_1, see https://www.gesis.org/cews/cews-home/20-jahre-cews
  background-size: cover;
  background-position-x: center !important;
  background-position-y: center !important;
  background-repeat: no-repeat;
  background-color: $gs_grey_10;
  // padding: $gs_pd_b1 $gs_pd_b2 $gs_pd_b1 $gs_pd_b2; // give padding so that image box also has some minimum size in responsive layouts
  &.gs_shade_1 {
    &::before {
      content: "";
      opacity: var(--gesis-standard-opacity);
      position: absolute;
      background-color: $gs_white;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
    > .frame {
    position: relative;
    }
  }
}

.gs_minheight_1 {
  min-height: 5rem;
}

.gs_minheight_2 {
  min-height: 10rem;
}

.gs_minheight_3 {
  min-height: 15rem;
}

.gs_mouseover_box {
  /*
  special cases:
  * http://localhost/angebot/daten-finden-und-abrufen
  * http://localhost/cews/drittmittelprojekte
  * http://localhost/cews/themen
  */
  background-color: $gs_blue;
  color: $gs_white;
  font-size: $gs_font_size_b2;
  font-weight: 300;
  text-align: center;
  overflow: hidden;
  svg { // for Beratungsbutton
    width: 3rem;
    vertical-align: middle;
    padding-left: $gs_pd_s1;
  }
  a, span {
    display: inline-flex;
    width: 100%;
    height: 100%;
    color: white;
    text-decoration: none;
    padding: $gs_pd_base;
    justify-content: center;
    align-items: center;
  }
  a {
    &:focus-visible {
      outline: white 2px dotted;
      outline-offset: -2px;
    }
    &:hover {
      background-color: var(--gesis-attention-color);
      color: $gs_white;
    }
  }
}

.gs_banner {
  margin: 0 -#{$gs_pd_b2};
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  position: relative; // TODO position: relative needed?
  background-repeat: no-repeat;
  background-color: $gs_grey_10;
  z-index: 0; // for smaller screens, put transparent banner text-box/slider/header topping in front of copyright, see below
  .gs_copyright {
    // padding: $gs_pd_s2 $gs_pd_s2 0 0;
    padding: $gs_pd_s2 $gs_pd_b2 0 0;
    font-size: $gs_font_size_s2;
    color: $gs_grey_80;
    position: absolute;
    right: 0;
    align-self: flex-start;
    z-index: -1 // for smaller screens, put copyright behind transparent banner text-box/slider/header topping, see above
  }
  > h1 { // shall only match Banner with layout 'Bild mit Headline'
    margin: 0;
    padding: $gs_pd_base $gs_pd_b2;
    background-color: #ffffffcc;
    width: 100%;
    align-self: flex-end;
  }
  .container {
    z-index: 1; // give banner container higher z-index than .gs_copyright's default z-index of 0 so that copyright information does not overlay container in small viewports
  }
  
}

// teaser - used as inline HTML content element style e.g. on the home landing page;
a:hover div.gs_teaser {
  background-color: $gs_grey_5;
}

div.gs_teaser {
  background-color: $gs_grey_10;
  &:hover {
    background-color: $gs_grey_5;
  }
  a {
    text-decoration: none;
  }
  img {
    display: block;
    margin: 0 auto;
  }
  h2, h3 {
    padding: 0;
    font-weight: normal;
    background-color: $gs_grey_60;
    color: $gs_white;
    text-align: center;
    // following rules are a workaround for wrapping-a-underlines to visually disappear
    // text-decoration-line: underline;
    // text-decoration-color: $gs_grey_60;
  }
  h3 {
    font-size: $gs_font_size_b1;
    line-height: 2.5rem;
  }
}

.gs_banner_height_1 {
  min-height: $gs_banner_height_1;
}

.gs_banner_height_2 {
  min-height: $gs_banner_height_2;
}

.gs_banner_height_3 {
  min-height: $gs_banner_height_3;
}

.gs_trans_text > * {
  background-color: rgba(255, 255, 255, 0.8);
  // padding: $gs_pd_b1;
  padding: $gs_pd_base;
  // backdrop-filter: blur(6px); // not available in Firefox as of 2021-11-12, https://caniuse.com/css-backdrop-filter
}

// Vertical news
.gs_news_vertical {
  .content {
    ul {
      padding: 0;
      margin-bottom: 0;
      li {
        margin: 0 0 $gs_pd_s2 0;
        background-color: $gs_white;
        list-style: none;
        padding: $gs_pd_base;
        a {
          display: inline-block;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}

// News lists and news detail
.news {
  /*
  .mediaelement {
    * {
      // put news details images and videos centered below each other
      display: block;
      margin: 0 auto;
      max-width: 100%;
      max-height: 600px;
      height: auto; // to scale proportionally
    }
  }
  */
  .news-img-wrap {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
  // News detail only
  &.news-single {
    .news-img-wrap {
      margin: $gs_pd_base 0;
    }
    .news-backlink-wrap {
      margin-top: $gs_pd_base;
    }
  }
}

// GESIS Microsites
.gs_ms_header {
  background-image: url("img/ms_banner/gs_ms_banner_default.jpg");
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: center;
  background-size: cover;
  > .row {
    min-height: 5rem;
  }
  h1 {
    font-size: calc(1.2rem + 1.14vw);
    color: $gs_grey_60;
    margin-bottom: 0;
    @include media-breakpoint-up(xl) {
      font-size: 2rem;
    }
  }
}

.gs_pagebrowser {
  // pagination widget e.g. for vertical news lists and GRIS service lists
  li {
    background-color: var(--gesis-accent-background-color);
    border: none;
    margin: 0;
    padding: 0;
    > * {
      padding: 0.5rem 0.8rem;
      display: inline-block;
    }
    a {
      text-decoration: none;
      &:hover, &[aria-current="page"] {
        color: $gs_white;
        background-color: var(--gesis-attention-color);
      }
    }
  }
}

.gs_newsletter {
  img {
    max-width: unset; // unset default "max-width: 100%;" for HTML e-mail layout preview purposes
  }
  tr {
    background-color: #fff;
  }
  tr:hover {
    background-color: #fff;
  }
}

.skip-links {
  display: flex;
  align-items: flex-end;
  transition: max-height ease-in-out 0.5s;
  background-color: $gs_blue;
  color: $gs_white;
  max-height: 0;
  padding: 0 $gs_pd_b2;
  &:focus-within {
    max-height: 500px;
  }
  * {
    font-size: $gs_font_size_b1;
  }
  ul, li {
    list-style: none;
    padding: 0;
  }
  a {
   color: $gs_white;
     &:hover {
      color: var(--gesis-attention-color);
    }
  }
}

.top { // those go-to-top links
  display: flex;
  flex-direction: row-reverse;
}

/* 2020 new lifecycle */
div[class*="gs_phase"] {
  background-color: $gs_grey;
  padding: $gs_pd_base;
  margin-bottom: $gs_pd_s2;
}

div.gs_phase_active, .gs_phases:hover {
  // for research data cycle
  // http://localhost/angebot
  // http://localhost/en/services
  background-color: var(--gesis-attention-color);
  a, p {
    color: $gs_white;
  }
}

[class^="icon-"], [class*=" icon-"] {
  // rule for icomoon GESIS-Web icon font to not underline icons when icon is inside a element
  display: inline-block;
  line-height: 1;
  vertical-align: -0.125em;
}

@include media-breakpoint-down(md) {
  main {
    --gesis-main-padding-x: #{$gs_pd_base};
  }
  
  .gs_banner {
    margin: 0 -#{$gs_pd_base};
    > h1 {
      padding: $gs_pd_base $gs_pd_base;
    }
  }
  
  .skip-links {
    padding: 0 $gs_pd_base;
  }
}

@media (prefers-reduced-motion) {
  :root {
    scroll-behavior: auto;
  }
  * {
    animation: none !important;
    transition-duration: 0s !important;
    }
}

@import 'gesis-table';
@import 'gesis-tabs';
@import 'gesis-accordion';
@import 'gesis-form';
@import 'gesis-ce';
@import 'gesis-search';
@import 'gesis-megamenu';
@import 'gesis-slider';
@import 'gesis-dialog';
@import 'gesis-cews';
@import 'gesis-eurobarometer';
@import 'gesis-online-populism';
@import 'gesis-ssoar';
@import 'gesis-wahlen';
@import 'gesis-eventnews';
@import 'gris-staff';
@import 'gris-service';
@import 'gesis-grid';
@import 'gesis-testimonials';
@import 'gesis-user-manual';
