.grid-general {
  // see also https://stackoverflow.com/a/70257340/923560
  > .row {
    display: grid;
    --row-gap: 1rem;
    // row-gap: var(--row-gap);
    grid-template-columns: 1fr;
    column-gap: var(--bs-gutter-x);
    padding-left: calc(var(--bs-gutter-x)* 0.5);
    padding-right: calc(var(--bs-gutter-x)* 0.5);
    margin-top: 0;
    // margin-bottom: calc(-1 * var(--bs-gutter-y)); // compensate last colum element's axis margin-bottom
    > .col {
      display: contents !important;
      &:nth-child(1) { // first .col
        --axis-number: 0;
        --col-number: 1;
      }
      &:nth-child(2) { // second .col
        --axis-number: 1;
        --col-number: 1;
      }
      &:nth-child(3) { // third .col
        --axis-number: 2;
        --col-number: 1;
      }
      &:nth-child(4) { // fourth .col
        --axis-number: 3;
        --col-number: 1;
      }
      &:not(:first-child) > :first-child {
        // in small screens, put vertical extra-gap *between* all columns
        margin-top: var(--bs-gutter-y);
        // margin-top: calc(var(--bs-gutter-y) - $gs_pd_base);
      }
      > * { // content elements within a .col
        // display: grid; // to have nested divs stretch height to grid cell height
        display: flex;
        flex-direction: column;
        // grid-column: calc(var(--col-number) / var(--col-amount) * var(--col-amount-effective));
        grid-row: calc(var(--axis-number) * var(--row-amount) + var(--row-number));
        grid-column: var(--col-number);
        // margin-bottom: auto;
        margin-bottom: 0;
        &:nth-child(1) { // first content element within a .col
          --row-number: 1;
        }
        &:nth-child(2) { // second content element within a .col
          --row-number: 2;
        }
        &:nth-child(3) { // third content element within a .col
          --row-number: 3;
        }
        &:nth-child(4) {
          --row-number: 4;
        }
        &:nth-child(5) {
          --row-number: 5;
        }
        &:nth-child(6) {
          --row-number: 6;
        }
        &:nth-child(7) {
          --row-number: 7;
        }
        &:nth-child(8) {
          --row-number: 8;
        }
        &:last-child {
          // margin-bottom: auto;
          margin-bottom: 0;
        }
        /* see https://stackoverflow.com/a/12198561/923560 */
        /* solution until widespread CSS :has() support */
        /* buggy as it does not get the row-amount of neighboring maximum row-amount */
        &:nth-last-child(1) {
          --row-amount: 1;
        }
        &:first-child:nth-last-child(2),
        &:first-child:nth-last-child(2) ~ * {
          --row-amount: 2;
        }
        &:first-child:nth-last-child(3),
        &:first-child:nth-last-child(3) ~ * {
          --row-amount: 3;
        }
        &:first-child:nth-last-child(4),
        &:first-child:nth-last-child(4) ~ * {
          --row-amount: 4;
        }
        &:first-child:nth-last-child(5),
        &:first-child:nth-last-child(5) ~ * {
          --row-amount: 5;
        }
        &:first-child:nth-last-child(6),
        &:first-child:nth-last-child(6) ~ * {
          --row-amount: 6;
        }
        &:first-child:nth-last-child(7),
        &:first-child:nth-last-child(7) ~ * {
          --row-amount: 7;
        }
        &:first-child:nth-last-child(8),
        &:first-child:nth-last-child(8) ~ * {
          --row-amount: 8;
        }
        > *:last-child {
          height: 100% // to have last (including only) child fill remaining vertical space.
          // Test case: textmedia with header and bodytext, e.g. https://www.gesis.org/allbus/download vs. https://www.gesis.org/development/css-grid
        }
      }
    }
    /* modern solution, requires :has() */
    &:has(.col > :nth-child(1)) > .col > * {
      --row-amount: 1;
    }
    &:has(.col > :nth-child(2)) > .col > * {
      --row-amount: 2;
    }
    &:has(.col > :nth-child(3)) > .col > * {
      --row-amount: 3;
    }
    &:has(.col > :nth-child(4)) > .col > * {
      --row-amount: 4;
    }
    &:has(.col > :nth-child(5)) > .col > * {
      --row-amount: 5;
    }
    &:has(.col > :nth-child(6)) > .col > * {
      --row-amount: 6;
    }
    &:has(.col > :nth-child(7)) > .col > * {
      --row-amount: 7;
    }
    &:has(.col > :nth-child(8)) > .col > * {
      --row-amount: 8;
    }
  }
}

@include media-breakpoint-up(sm) {
  .grid-general {
    &.frame-type-gesis-web-2col, &.frame-type-gesis-web-4col {
      > .row {
        grid-template-columns: 1fr 1fr;
        > .col {
          &:nth-child(1) {
            --axis-number: 0;
            --col-number: 1;
          }
          &:nth-child(2) {
            --axis-number: 0;
            --col-number: 2;
          }
          &:nth-child(3) {
            --axis-number: 1;
            --col-number: 1;
          }
          &:nth-child(4) {
            --axis-number: 1;
            --col-number: 2;
          }
        }
      }
    }
    &.frame-type-gesis-web-2col {
      > .row {
        > .col {
          > :first-child {
            margin-top: 0;
          }
        }
      }
    }
    &.frame-type-gesis-web-4col {
      > .row {
        > .col {
          &:nth-child(2) > :first-child {
            margin-top: 0;
          }
        }
      }
    }
  }
}

@include media-breakpoint-up(md) {
  .grid-general {
    &.frame-type-gesis-web-3col {
      > .row {
        grid-template-columns: 1fr 1fr 1fr;
        > .col {
          &:nth-child(1) {
            --axis-number: 0;
            --col-number: 1;
          }
          &:nth-child(2) {
            --axis-number: 0;
            --col-number: 2;
          }
          &:nth-child(3) {
            --axis-number: 0;
            --col-number: 3;
          }
          > :first-child {
            margin-top: 0;
          }
        }
      }
    }
  }
}

@include media-breakpoint-up(lg) {
  .grid-general {
    &.frame-type-gesis-web-4col {
      > .row {
        grid-template-columns: 1fr 1fr 1fr 1fr;
        > .col {
          &:nth-child(1) {
            --axis-number: 0;
            --col-number: 1;
          }
          &:nth-child(2) {
            --axis-number: 0;
            --col-number: 2;
          }
          &:nth-child(3) {
            --axis-number: 0;
            --col-number: 3;
          }
          &:nth-child(4) {
            --axis-number: 0;
            --col-number: 4;
          }
          > :first-child {
            margin-top: 0;
          }
        }
      }
    }
    
    &.frame-type-gesis-web-2col-1third2thirds {
      > .row {
        grid-template-columns: 1fr 1fr 1fr;
        > .col {
          &:nth-child(1) {
            --axis-number: 0;
            --col-number: 1;
          }
          &:nth-child(2) {
            --axis-number: 0;
            --col-number: 2;
            > * {
              /*
              this rule is important for
              having the column gutter line up identically for both
              .grid-general and :not(.grid-general)
              gesis-web-2col-1third2thirds containers
              */
              grid-column: 2 / span 2;
            }
          }
          > :first-child {
            margin-top: 0;
          }
        }
      }
    }
  }
}
