@import '../js/accessible-slider/src/tiny-slider';

.tns-outer {
  display: grid;
  flex-flow: column;
  .tns-controller {
    order: 0;
    margin-bottom: $gs_pd_base;
    display: flex;
    flex-flow: row wrap;
    button {
      padding: 0;
      width: 44px;
      height: 44px;
      vertical-align: middle;
      background-size: contain;
      &.tns-prev {
        background-image: url('img/icons/white/appbar.control.rewind.svg');
      }
      &.tns-next {
        background-image: url('img/icons/white/appbar.control.fastforward.svg');
      }
      &.tns-autoplay {
        margin-right: $gs_pd_s2; // workaround to give button some space to visually, but not DOM-relative consecutive button
        &[data-action="start"] {
          background-image: url('img/icons/white/appbar.control.resume.svg');
        }
        &[data-action="pause"] {
          background-image: url('img/icons/white/appbar.control.pause.svg');
        }
      }
    }
  }
  .tns-nav {
    order: 2;
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    text-align: center;
    align-items: center;
    justify-content: center;
    margin-top: $gs_pd_base;
    > [aria-controls] {
      width: 15px;
      height: 15px;
      padding: 0;
      margin: 5px 10px;
      border-radius: 50%;
      background: #bbb;
      border: 0;
      &:hover {
        background: $gs_grey_40;
      }
    }
    > [aria-selected="true"] {
      background: $gs_grey_60;
    }
  }
  .tns-ovh {
    order: 1;
    .tns-inner {
      display: block;
      .tns-horizontal {
        margin: 0;
      }
      .tns-slider {
        display: flex;
        padding: 0;
        &.tns-animating {// class added on ul during animation
          user-select: none; // don't make hectic text selection during dragging
          .tns-item:focus {
           outline: none; // don't show li focus outline during animation
          }
        }
        .tns-item {
          margin: 0;
          padding: 0;
          min-height: 44px; // provide a minimum height for swipe usability, see https://medium.com/@zacdicko/size-matters-accessibility-and-touch-targets-56e942adc0cc
          background-clip: content-box;
          list-style-type: none;
          &:focus-visible, &:focus-within { // here, we show the outline, because focus on active slide is a unique usability feature
            outline: var(--gesis-attention-color) 2px dotted;
            outline-offset: -2px;
          }
        }
      }
    }
  }
}

.gs_news_horizontal, .gs_rss_slider, .gs_news_vertical, .gs_single_slider_wrapper {
  --gesis-background-color: #{$gs_grey_10};
  --gesis-accent-background-color: #{$gs_white};
  
  background-color: var(--gesis-background-color);
  text-align: center;
  
  *[itemprop="headline"], .feed_item_title {
    font-weight: bold;
  }
  .gs_3col_slider [role="tabpanel"] {
    min-height: 16rem;
    background-color: $gs_white;
    .gs_news_image {
      // text-align: center;
      overflow: hidden;
      height: 8rem;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
}
