.gesis-color-white {
  &table, &table.ce-table, &table.dataTable, table, table.ce-table, table.dataTable {
    --gesis-table-border-color: var(--gesis-color-grey-10);
    caption {
      --gesis-table-caption-background-color: var(--gesis-color-white);
    }
    tr {
      --gesis-table-tr-background-color: var(--gesis-color-white);
    }
  }
}
