table, table.ce-table { // also adding .ce-table to overrule TYPO3's own Fluid CSS rule
  --gesis-table-border-width: 2px;
  --gesis-table-border-color: var(--gesis-color-white);
  
  width: 100%;
  
  // improving Bootstrap's opinionated table caption style ( https://getbootstrap.com/docs/5.1/content/tables/#captions )
  caption-side: top;
  caption {
    --gesis-table-caption-color: var(--gesis-color-black);
    --gesis-table-caption-background-color: var(--gesis-color-grey-5);
    
    font-family: var(--gesis-font-serif);
    
    color: var(--gesis-table-caption-color);
    
    padding-left: calc( var(--gesis-padding-base) - var(--gesis-table-border-width) );
    /*
    background-color: var(--gesis-table-caption-background-color);
    
    border-style: solid;
    
    border-top-color: var(--gesis-table-border-color);
    border-right-color: var(--gesis-table-border-color);
    border-bottom-color: transparent;
    border-left-color: var(--gesis-table-border-color);
    
    // different to tr/th/td, caption element does not use border-collapse 
    // therefore to align caption element text to text inside th/td,
    // caption's margin, padding-left, and border have all to be handled specially
    // including usage of calc(...)
    border-width: var(--gesis-table-border-width);
    
    background-image:
      linear-gradient(0deg, var(--gesis-table-caption-background-color), var(--gesis-table-caption-background-color)),
      linear-gradient(0deg, var(--gesis-table-caption-background-color), var(--gesis-table-caption-background-color)),
      linear-gradient(0deg, var(--gesis-table-border-color), var(--gesis-table-border-color));
    background-clip: content-box, padding-box, border-box;
    */
  }
  tr {
    --gesis-table-tr-background-color: var(--gesis-color-grey-10);
    background-color: var(--gesis-table-tr-background-color);
    &:hover {
      background-color: var(--gesis-color-grey-20);
    }
    th, td {
      padding: var(--gesis-padding-s2) var(--gesis-padding-base);
      vertical-align: top;
      border: var(--gesis-table-border-width) solid var(--gesis-table-border-color);
      &.img { // inside <td class=".img"> table cells, give img elements their self-specified width and height
        padding: 0;
        img {
          max-width: unset;
        }
      }
    }
  }
  thead th {
    background-color: var(--gesis-color-grey-40);
    color: var(--gesis-color-white);
  }
  tbody th {
    background-color: var(--gesis-color-grey-20);
  }
}

table.dataTable {
  --gesis-table-border-width: 1px;
  tbody tr, tr {
    --gesis-table-tr-background-color: var(--gesis-color-grey-10);
    background-color: var(--gesis-table-tr-background-color);
    &:hover {
      background-color: var(--gesis-color-grey-20);
    }
    th, td {
      padding: var(--gesis-padding-s2) var(--gesis-padding-base);
      vertical-align: top;
      border: var(--gesis-table-border-width) solid var(--gesis-table-border-color);
      &.img { // inside <td class=".img"> table cells, give img elements their self-specified width and height
        padding: 0;
        img {
          max-width: unset;
        }
      }
    }
  }
}
