@media  (min-width: 991.98px) and (hover: hover) and (pointer: fine) {
  .gs_mm_toggle_button {
    display: none;
  }
  
  .gs_megamenu_nav {
    position: relative; // so that nested pop-ups have a context for their width: 100%, see https://www.gesis.org/styleguide/verwendung/widgets/megamenue
    // display: flex;
    // flex-wrap: wrap;
    background-color: $gs_blue_15;
    
    .gs_megamenu {
      padding-right: 2rem;
      padding-left: 2rem;
      display: flex;
      flex-wrap: wrap;
      font-weight: bold;
      // background-color: $gs_blue_15;
      margin: 0;
      a {
        text-decoration: none;
        &:focus-visible {
          outline-offset: -2px;
        }
      }
      .gs_active > a {
          color: var(--gesis-attention-color);
      }
      ul, li {
        list-style-type: none;
        padding: 0;
        break-inside: avoid;
      }
      > li {
        a {
          padding: 0.4rem $gs_pd_s2;
          display: inline-block; // for hover to fill completely
          &:hover {
            color: $gs_white;
            background-color: var(--gesis-attention-color);
          }
        }
        > a {
          + ul {
            display: none;
          }
          &[aria-haspopup="true"] {
            &::after {
              font-family: bootstrap-icons;
              content: '\f229'; // similar to ''
              font-weight: normal;
              margin-left: 0.25rem;
              font-size: 0.7rem;
            }
          }
          &[aria-expanded="true"] {
            + ul {
              display: block;
              padding: $gs_pd_base;
              position: absolute;
              left: 0;
              z-index: $gs_z_menu;
              background-color: #ededed;
              width: 100%;
              column-count: 3;
              column-gap: 0;
              column-rule: 0.1rem solid white;
              > li {
                &:not(:last-child) {
                  margin: 0 0 $gs_pd_base 0;
                }
                a {
                  font-size: $gs_font_size_s1;
                  display: block; // block to have hover/select area fill whole available horizontal area
                  padding: 0.4rem $gs_pd_base;
                }
                ul {
                  li {
                    a {
                      font-weight: normal;
                      padding-left: 2rem;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  
}

// max-width: 991.98px equals @include media-breakpoint-down(lg). Comma behaves like logical or operator
@media (max-width: 991.98px), (hover: none), (pointer: coarse) {
  .gs_mm_toggle_button {
    background-image: url(img/icons/white/appbar.menu.svg);
    background-size: contain;
    width: 44px;
    height: 44px;
    &[aria-expanded="true"] {
      background-image: url(img/icons/white/appbar.close.svg);
    }
    &[disabled] {
      background-image: none;
    }
    &:hover {
      /* 
      on touch-only devices, touching/clicking buttons gives them :focus and
      :hover state AND keeps these states until something else is touched. Also,
      buttons in GESIS-Web design have a strong hover color (orange, CEWS lila).
      This looks too dominant for this menu button in particular, therefore
      unsetting to default non-hover color.
      */
      background-color: var(--gesis-normal-color);
    }
  }
  
  .gs_megamenu_nav {
    position: relative; // so that nested pop-ups have a context for their width: 100%, see https://www.gesis.org/styleguide/verwendung/widgets/megamenue
    .gs_megamenu {
      &[data-open="true"] {
        /* WIP idea to get rid of quasi-redundant data-open attribute and instead
        leverage controlling menubutton's aria-expanded attribute:
        .gs_mm_toggle_button[aria-expanded="true"] ~ .gs_megamenu_nav > .gs_megamenu { display: block; ... }
        ... but this idea requires menubutton and .gs_megamenu_nav wrapper to be DOM siblings.
        */
        display: grid;
      }
      display: none; //FIXME: accessiblity issue: "display: none" causes the wrapping .gs_megamenu_nav's nav role to be ignored,
      // causing this important navigational landmark to be removed from the accessibility tree (e.g., not being listed in landmarks outline) while
      // on small screens and while megamenu is closed (i.e., as long as megamenu toggle button has not been pressed to reveal megamenu).
      // One solution to fix this could be to instead use ".visually-hidden"-similar rules while small-screen megamenu is closed, and removing these
      // rules while opened.
      
      position: absolute;
      padding: 0;
      border-top: 1px solid white;
      border-bottom: 1px solid white;
      font-weight: bold;
      animation: 500ms linear 0ms 1 normal none running slide-down;
      background-color: $gs_blue;
      .gs_active > a {
        color: var(--gesis-attention-color);
      }
      a {
        padding: 1rem;
        grid-column: 2 / 3;
        font-size: $gs_font_size_s1;
        text-decoration: none;
        &:focus-visible {
          outline-offset: -2px;
        }
      }
      li {
        display: grid;
        grid-template-columns: 3.5rem 1fr;
        border-bottom: 1px solid white;
        &:last-child {
          border-bottom: none;
        }
        &.gs_sub { // if has class .gs_sub, then it has sublist
          cursor: pointer; // show pointer cursor (on hover) on this non-a/non-link element because JavaScript allows expanding and collapsing this list
          &::before {
            font-family: bootstrap-icons;
            content: '\f231'; // similar to ''
            font-weight: normal;
            color: white;
            text-align: center;
            padding: 1rem 0 0 0;
          }
        }
        &:not(.gs_sub) {
          // if it does not have class .gs_slub, then it has no sublist
          // because of default cursor inheritance, here cursor must be
          // explicitly set to default
          cursor: default;
        }
        ul {
          display: none;
        }
        a[aria-expanded="true"] + ul {
          display: grid;
          padding: 0;
          animation: 500ms linear 0ms 1 normal none running slide-down;
          grid-column: 2 / 3;
        }
      }
      > li {
        > a {
          background-color: #e5e5e5;
        }
        > ul {
          background-color: $gs_blue_60;
          > li {
            > a {
              background-color: #efefef;
            }
            > ul {
              background-color: $gs_blue_30;
              > li {
                > a {
                  background-color: #f5f5f5;
                }
              }
            }
          }
        }
      }
    }
    
  }
}

@keyframes slide-down {
  0% {
    max-height: 0px;
    overflow: hidden;
  }
  100% {
    max-height: 1000px;
    overflow: hidden;
  }
}
@keyframes slide-up {
  0% {
    max-height: 1000px;
    overflow: hidden;
  }
  100% {
    max-height: 0px;
    overflow: hidden;
  }
}
