// grayed-out backdrop for non-native (i.e. non-<dialog>)/<div>-based dialog modals
.modal .modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, var(--gesis-backdrop-opacity));
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9000;
}

dialog, .modal .modal__container {
  border: none;
  background-color: #fff;
  padding: $gs_pd_base;
  max-width: map-get($container-max-widths, "sm");
  max-height: 100vh;
  overflow-y: auto;
  
  // grayed-out backdrop for native/<dialog>-based dialog modals
  &::backdrop {
    // ::backdrop CSS custom properties are defined in gesisweb.scss because of
    // https://stackoverflow.com/a/63322762
    // https://developer.mozilla.org/en-US/docs/Web/CSS/::backdrop
    // "::backdrop neither inherits from nor is inherited by any other elements"
    background: rgba(0, 0, 0, var(--gesis-backdrop-opacity));
  }
  
  header, .modal__header {
    display: flex;
    justify-content: space-between;
    
    button[data-action-close], button[data-action-cancel], .modal__close {
      color: #000;
      background: transparent;
      padding: 0;
      margin: 0;
      
      &:hover {
        color: var(--gesis-attention-color);
        background-color: transparent;
      }
      
      &:focus-visible {
        outline-color: var(--gesis-attention-color);
        outline-offset: 4px;
      }
      
      &::before {
        content: "";
      }
      
    }
  }
  
  .dialog-content, .modal__content {
    margin-top: $gs_pd_b2;
    margin-bottom: $gs_pd_b2;
  }
  
  footer, .modal__footer {
    display: flex;
    justify-content: flex-end;
    padding: 0;
  }
}

@keyframes mmfadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}

@keyframes mmfadeOut {
    from { opacity: 1; }
      to { opacity: 0; }
}

.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
}

.micromodal-slide[aria-hidden="false"] .modal__overlay {
  animation: mmfadeIn .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__overlay {
  animation: mmfadeOut .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}
