@mixin gesis-style-list {
  /* An opinionated template style for ul, ol, and li which works well for GESIS-Web content,
   * but easily messes up non-GESIS Web styles, such as Bootstrap components,
   * therefore provided for mixing in thoughtfully.
   */
  ul {
    li {
      list-style-type: square;
    }
  }
  ul, ol {
    &:last-child {
      margin-bottom: 0;
    }
    li {
      margin-bottom: 0.5rem;
      &:last-child {
        margin-bottom: 0;
      }
      ul, ol {
        margin-top: 0.5rem; // for recursively nested lists
      }
    }
  }
}

.frame-type-menu_subpages {
  // content element type "Unterseiten-Auflistung", e.g.
  // https://www.gesis.org/cews/portfolio/digitale-angebote/statistiken/thematische-suche
  @include gesis-style-list;
}

main {
  > .frame {
    margin-top: $gs_pd_b2;
    margin-bottom: $gs_pd_b2;
    &:first-child {
      &.frame-type-gesis-web-banner, &.frame-list-type-cbgooglemaps_quickgooglemap {
        // when hero banner or maps is first content element within main, give it negative margin to merge with main's padding'
        margin-top: -$gs_pd_b2;
      }
    }
    &:last-child {
      &.frame-type-gesis-web-banner, &.frame-list-type-cbgooglemaps_quickgooglemap {
        // when hero banner or maps is last content element within main, give it negative margin to merge with main's padding'
        margin-bottom: -$gs_pd_b2;
      }
    }
  }
}

.frame {
  margin-top: $gs_pd_base;
  margin-bottom: $gs_pd_base;
  &:first-child { // e.g. first content element within container wrapper
    margin-top: 0;
  }
  &:last-child { // e.g. last content element within container wrapper
    margin-bottom: 0;
  }
}

.frame-type-gesis-web-1col,
.frame-type-gesis-web-2col,
.frame-type-gesis-web-2col-1third2thirds,
.frame-type-gesis-web-3col,
.frame-type-gesis-web-4col {
  // there is at least one corner case, namely first contact button, then columns element
  // which results in too much margin between them. Using overflow: hidden on the columns
  // element container helps remove this unwanted extra margin. It may or may not follow
  // Bootstrap's suggestion to use .overflow-hidden when using Bootstrap gutters,
  // see https://getbootstrap.com/docs/5.1/layout/gutters/#vertical-gutters
  // UPDATE: but this breaks sticky-top on elements within container,
  // e.g., http://localhost/cews/news-events/veranstaltungskalender with sticky-top
  // on left calendar sheet element, therefore not using overflow: hidden
  // overflow: hidden;
  .col {
    &:empty {
      // if column doesn't have content, make sure it doesn't occupy any spsace at all
      // important for e.g. 2-col or 3-col elements where not all columns have content,
      // as otherwise empty areas will show up in  responsive stacked column layot.
      // the :empty pseudo-class is tricky, as it will not be activate when there is
      // solely whitespace between <col>    </col> - such whitespace is typical
      // to exist with TYPO3 Fluid-generated templates such as Gs_2_col.html.
      // see https://developer.mozilla.org/en-US/docs/Web/CSS/:empty
      // examples
      // http://localhost/angebot/daten-aufbereiten-und-analysieren
      // http://localhost/angebot/archivieren-und-teilen
      // http://localhost/forschung/tagungen-und-konferenzen/gesis-tagungen
      margin-top: 0;
    }
  }
}

