/*
.ce-center {
  display: flex;
  justify-content: center;
}
*/

.ce-center .ce-outer {
  position: unset;
  float: unset;
  right: unset;
}

.ce-center .ce-inner {
  position: unset;
  float: unset;
  right: unset;
}

.ce-left {
  .ce-gallery {
    // float: unset !important;
    .ce-row {
      // http://localhost/angebot/daten-finden-und-abrufen/european-values-study
      justify-content: left;
    }
  }
}

.ce-right {
  .ce-gallery {
    // float: unset !important;
    .ce-row {
      // prime example: http://localhost/en/eurobarometer-data-service/home
      // example: http://localhost/en/issp/home , very bottom right images
      justify-content: right;
    }
  }
}

.ce-row {
  display: flex;
  flex-wrap: wrap; // needed for e.g. several YouTube iframes in CKEditor gallery not to overflow their boundaries
  justify-content: center;
}

/*
corner cases:
* http://localhost/cews/cews-home/zielgruppen/gleichstellungsakteurinnen
* https://test.gesis.org/development/ckeditor
* http://localhost/datenservices/home
*/
.ce-column {
  float: unset;
}

.ce-textpic, .ce-image, .ce-nowrap .ce-bodytext, .ce-gallery, .ce-row, .ce-uploads li, .ce-uploads div {
  // overwrite CKEditor default overflow: hidden in order to show focus ring correctly
  overflow: unset;
  &::after {
    // clearfix for floating images to make floating images occupy their vertical space
    // prime example: http://localhost/en/issp/home
    display: block;
    content: "";
    clear: both;
  }
}

.ce-bodytext {
  @include gesis-style-list;
  img {
    /*
    give CKEditor inline images some margin to any possibly around-floating text
    and above-below-directly floating other inline images, but not its bottom,
    because at the bottom text will already provide line height space.
    see http://localhost/development/ckeditor
    */
    margin: $gs_pd_s2 $gs_pd_s2 0 $gs_pd_s2;
    
    /*
    "clear: both" will make sure that several floated images will not awkwardly
    stack horizontally. This is something we want for
    https://test.gesis.org/piaac/fuer-teilnehmende/beispielaufgaben
    but it may be unwanted for other instances of CKEditor inline-floated images.
    Time will tell.
    Provoking example: https://test.gesis.org/piaac/fuer-teilnehmende/beispielaufgaben
    Can be trumped by explicitly adding the following classes on CKEditor inline images:
    .clear-none ... .clear-left ... .clear-right ... .clear-both
    Very handy in combination with the following classes:
    .float-start ... .float-end
    */
    clear: both;
  }
  
  blockquote {
    border-style: solid;
    border-color: #ccc;
    border-width: 0 0 0 0.3rem; // 1.7rem + 0.3rem equals ol/ul's padding-left 2rem
    padding-left: 1.7rem; // 1.7rem + 0.3rem equals ol/ul's padding-left 2rem
  }
  
  // last child / last direct ancestor shall not produce additional spacing.
  // Using child combinator, because otherwise tables within content elements will have
  // their tds match, see http://localhost/kontakt
  > *:last-child {
    margin-bottom: 0;
    padding-bottom: 0;
  }
  
  /*
   .align-left, .align-center, .align-right, and .align-justify are vestigial CSS classes
   found in CKEditor content elements created no later than around 2020-04-29:
     SELECT * FROM tt_content WHERE bodytext LIKE '%align-center%' AND t3_origuid = 0 and deleted = 0 AND hidden = 0 ORDER BY crdate desc;
   
   Blaming commit: https://github.com/TYPO3-CMS/rte_ckeditor/commit/76ae7a714be53dc63446ded38527201a20c897dc
   
   TODO:
     * SQL-Migrate old class names ot new class names inside tt_content bodytext
     * Then remove below .align-* rules as Bootstrap provides .text-*
  */
  .align-left { // NEW: GESIS-Web's own .text-left ... or no class at all by implication, though CSS inheritance may override default "text-align: left;"
    text-align: left;
  }
  .align-center { // NEW: Bootstrap 5's .text-center
    text-align: center;
  }
  .align-right { // NEW: GESIS-Web's own .text-right
    text-align: right;
  }
  .align-justify { // NEW: GESIS-Web's own .text-justify
    text-align: justify;
  }

  .pagination {
     li:first-child {
       list-style-type: none
     }
  }

  .dataTables_wrapper {
    width: 100%
  }
}

// overrule default fluid_styled_content ce-table style with GESIS table styling
.ce-table th, .ce-table td {
  padding: var(--gesis-padding-s2) var(--gesis-padding-base);
}

@include media-breakpoint-down(md) {
  .ce-gallery, .ce-column {
    /* on smaller screens, make CKEditor text not flow next to image, but keep image separate
    * http://localhost/cews/themen/familienfreundliche-wissenschaft
    * http://localhost/cews/themen/gender-bias
    * http://localhost/home (Social-Media-Logos am Ende der Seite)
    * http://localhost/development/ckeditor (Galeriebilder oben)
    */
    float: unset !important;
    display: flex;
    justify-content: center;
  }
  
  // unset CKeditor gallery nonsense
  .ce-center .ce-outer {
    position: unset;
    float: unset;
    right: unset;
  }
  
  // unset CKeditor gallery nonsense
  .ce-center .ce-inner {
    position: unset;
    float: unset;
    right: unset;
  }
}

.dataTables_filter .form-control, .dataTables_length .form-select{
  box-shadow: none;
  -webkit-box-shadow: none;
}

.dataTables_paginate .page-link {
  box-shadow: none;
  -webkit-box-shadow: none;
