.gs_search {
  position: relative; // so that .gs_hitlist search result has a relative context for its width: 100%
  background-color: $gs_blue;
  color: $gs_white;
}

.gs_search_clear {
  color: $gs_grey_20;
  background-color: transparent;
  visibility: hidden;
  padding: 0;
  margin-left: -1.5rem; // to move clear button inside search box
  z-index: 1;
  &:hover {
    color: var(--gesis-attention-color);
    background-color: white;
  }
}

.gs_search_toggle {
  &:hover {
    background-color: unset;
  }
}

.gs_combobox[aria-expanded="false"] .gs_search_toggle::after {
  cursor: pointer;
  font-family: bootstrap-icons;
  content: '\f229';
}
.gs_combobox[aria-expanded="true"] .gs_search_toggle::after {
  cursor: pointer;
  font-family: bootstrap-icons;
  content: '\f235';
}

.gs_combobox .spinner-border {
  visibility: hidden;
}

.gs_combobox.loading .spinner-border {
  visibility: visible;
}

.gs_combobox[aria-expanded="false"] .gs_hitlist {
  display: none;
}

.gs_combobox[aria-expanded="true"] .gs_hitlist {
  display: block;
}

.gs_searchterm {
  // width: 100%;
  // padding-right: 1.5rem; // so that conditional clear button does not overlay over long text input
  &::-webkit-search-cancel-button {
    display: none; // hide ugly automatic Chromium clear button. We have implemented out own, better clear button
  }
}

.gs_searchterm:not(:placeholder-shown) ~ .gs_search_clear {
  // workaround to show clear button only when search input element has input, see https://stackoverflow.com/a/35302732/923560;
  visibility: visible;
}

.gs_hitlist {
  > div {
    color: $gs_blue;
    position: absolute;
    z-index: $gs_z_search;
    background: white;
    left: 0;
    
    .gs_arrow_right {
      min-width: 2rem;
      background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' preserveAspectRatio='none' viewBox='0 0 10 20'><polygon points='0,0 10,10 0,20' style='fill:%2358748f' /></svg>");
      background-size: 100% 100% // for Firefox stretching
    }
    
    [class*="gs_type"] {
      background-repeat: no-repeat;
      padding: $gs_pd_s1 $gs_pd_s2 $gs_pd_s1 $gs_pd_b2;
      text-decoration: none;
      color: $gs_white;
      font-size: $gs_font_size_s1;
      background-position:0 $gs_pd_s2;
      justify-content: space-between;
      .gs_count {
        border-left: 1px solid white;
        padding-left:$gs_pd_s2;
      }
    }
    
    .gs_recommendations {
      .tns-item {
        visibility: visible !important; // search results are not tabbable, but selectable by arrow keys up/down. Therefore, we always make them visible
      }
      .gs_recommendation_item {
        // relative position for Bootstrap 5-style stretched-link card behavior,
        // see https://getbootstrap.com/docs/5.1/helpers/stretched-link/
        position: relative;
        
        background: $gs_grey_10;
        display: flex !important;
        flex-direction: column;
        &:hover {
            background: $gs_grey_20;
        }
        > .recommendation-item-header { // "Angebot", "Veranstaltung", "Mitarbeiter*in" headline
          // font-weight: bold;
          font-size: 1.3rem;
          text-align: center;
          color: $gs_white;
          background-color: $gs_grey_60;
          padding: $gs_pd_s3 0;
          margin-bottom: 0;
        }
        > a {
          text-decoration: none;
          flex-grow: 1; // to allow for trick of having .person on bottom of card. Important for cases where there are persons with and without photo for consistent layout. Needs height: 100% on .person class as well to work
          &:hover {
            color: $gs_blue;
          }
          &::after {
            // for Bootstrap 5-style stretched-link behavior, see above at ".gs_recommendation_item"
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            z-index: 1;
            content: "";
          }
        }
        
        .gs_product, .gs_training { // recommendations "Angebot" and "Veranstaltung"
          .recommendation-item-title { // GWS returns <h3> which gets modified by connector to <div class="recommendation-item-title">
            font-weight: bold;
            font-size: $gs_font_size_base;
            text-align: center;
            color: $gs_blue;
            background: transparent;
          }
          p {
            font-size: $gs_font_size_s1;
            // the following magic is for cutting off text from GESIS Search which is too long
            // see https://stackoverflow.com/a/13924997/923560
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 15;
            text-overflow: ellipsis;
            -webkit-box-orient: vertical;
            br {
              content: '';
              display: block;
              margin-bottom: $gs_pd_s4;
            }
          }
        }
        
        .vcard { // recommendation "Mitarbeiter*in"
          display: flex !important;
          flex-direction: column;
          flex: 1;
          height: 100%; // for stretching in order to have .person always align to bottom
          .photo {
            margin-bottom: $gs_pd_s2;
            img {
              width: 8rem;
              height: auto;
            }
          }
          .person {
            margin-top: auto; // pull name down in case a person does not have a photo to keep consistent layout
            font-size: $gs_font_size_s1;
            .recommendation-item-title { // GWS returns <h3> which gets modified by connector to <div class="recommendation-item-title">
              font-size: $gs_font_size_b1;
            }
            .gs_description {
              padding-right: $gs_pd_s5;
            }
          }
        }
      }
      
    }
  }

}

