// GWS combobox arrow key navigation highlighting
.result.focused, .focused-cell {
  outline: var(--gesis-attention-color) 2px dotted;
  visibility: visible !important; // for carousel sliders which are currently not shown
}

@include media-breakpoint-down(lg) {
  .gs_search {
    label {
      display: none;
    }
  }
}

.animation-spin {
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(1turn);
  }
}
